body {
  font-family: Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #ffffff;
}

::-webkit-scrollbar-thumb {
  background: rgb(0, 0, 0);
  border-radius: 10px;
  box-shadow: 1px 1px 20px 20px #000000;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(0, 0, 0);
}
hr {
  border: 1px solid #ededed;
}

::selection {
  background: tomato;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: inherit;
  justify-content: space-between;
}

.App-wrapper {
  height: 100vh;
}

.App-wrapper-work {
  width: 100%;
}
.App-work {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

.App-wrapper-about {
  width: 100%;
}
.App-about {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

@font-face {
  font-family: "Inter-regular";
  src: url(fonts/Inter-Regular.otf);
  font-style: normal;
}
@font-face {
  font-family: "Inter-black";
  src: url(fonts/Inter-Black.otf);
  font-style: normal;
}

@font-face {
  font-family: "Inter-bold";
  src: url(fonts/Inter-Bold.otf);
  font-style: normal;
}

.header-blur {
  display: flex;
  justify-content: space-between;
  z-index: 9;
}

.header-wrapper {
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 9;
}

.logo {
  align-self: center;
  font-family: Inter-black;
  font-weight: 100;
  color: rgb(52, 62, 73);
  text-decoration: none;
}

.unordered {
  display: flex;
  text-decoration: none;
  list-style-type: none;
  align-items: center;
}

.unordered > li {
  margin-right: 40px;
  font-family: Inter-regular;
  font-weight: 100;
  text-decoration: none;
}

.header-tags {
  text-decoration: none;
  color: black;
  letter-spacing: -1px;
}

.header-tags > img {
  height: 12px;
}

.logo-wrapper {
  align-self: center;
  width: 150px;
}

.body-container-padder {
  display: flex;
  padding: 1rem;
  padding-top: 3rem;
  justify-content: center;
}

.main-emoji {
  font-size: 150px;
}

.main-emoji > span {
  padding: 0 30px;
}

.text {
  text-align: start;
}
.text-wrapper {
  font-family: "Inter-bold";
  font-size: 32px;
  letter-spacing: -2px;
}

.text-div {
  width: 550px;
}

.get-in-touch {
  margin-top: 20px;
  font-family: "Inter-regular";
  display: flex;
  align-items: center;
}

.wave {
  cursor: grab;
  z-index: 0;
  filter: drop-shadow(15px 15px 8px rgba(211, 211, 211, 0.507));
}

img {
  height: 100px;
  filter: drop-shadow(20px 20px 18px rgba(211, 211, 211, 0.507));
}

.wave:hover {
  animation-name: wave-animation;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
  z-index: 0;
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-5deg);
  }
  20% {
    transform: rotate(6deg);
  }
  30% {
    transform: rotate(-5deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.text-react {
  background-color: #61dafa;
  color: white;
  padding: 0 5px;
  transition: 50ms ease-in-out;
  border-radius: 10px;
  display: inline-block;
}

.link-tags {
  text-decoration: none;
}

.active {
  transform: translateY(-5px);
}

.text-node {
  background-color: #70a761;
  color: white;
  padding: 0 5px;
  transition: 50ms ease-in-out;
  border-radius: 10px;
  display: inline-block;
}

.text-react > a,
.text-node > a {
  color: white;
}

.shadow-game {
  box-shadow: inset 0 -3px 0 tomato;
  padding: 5px 10px;
  transition: 80ms cubic-bezier(0.77, 1.12, 0.8, 1.07);
  cursor: pointer;
  text-decoration: none;
  color: black;
}
.shadow-game-la {
  box-shadow: inset 0 -3px 0 #0085ff;
  padding: 5px 2px;
  transition: 80ms cubic-bezier(0.77, 1.12, 0.8, 1.07);
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.fill-link{
  box-shadow: inset 0 -33px 0 #0085ff;
  color: white;

}

.fill {
  box-shadow: inset 0 -33px 0 tomato;
  color: white;
  
}

.pointing-finger {
  margin: 0px 5px 0px 0px;
  font-size: 16px;
}

.word {
  display: flex;
  justify-content: center;
  height: 40px;
  align-items: center;
}

.word span {
  font-family: "Inter-bold";
  font-size: 16px;
  position: absolute;
  opacity: 0;
  overflow: hidden;
  animation: animate 12s linear infinite;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }

  2% {
    opacity: 1;
    transform: translateY(0px);
  }

  15% {
    opacity: 1;
    transform: translateY(0px);
  }

  19% {
    opacity: 1;
    transform: translateY(0px);
  }
  20% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 0;
    transform: translateY(10px);
  }
}

.word span:nth-child(1) {
  animation-delay: 0s;
}
.word span:nth-child(2) {
  animation-delay: 4s;
}
.word span:nth-child(3) {
  animation-delay: 8s;
}

.footer {
  width: 100%;
}

.night-background {
  background: #252c30;
  transition: 200ms ease-in-out;
}

.wave-night {
  filter: none;
}

.night-text {
  color: white;
}

.image-filter {
  filter: invert(1);
}

.luvstay-image {
  height: 250px;
}

.just-chat-image {
  height: 284px;
}
.registration-image {
  height: 294px;
}

.scroll-container {
  position: relative;
  text-align: center;
  height: 30px;
  margin-bottom: 70px;
}

.auto-scroll {
  font-family: Inter-bold;
  animation: animate 5.4s linear infinite 0s;
  position: absolute;
  display: inline-block;
  opacity: 0;
  margin: 0 0;
  height: 40px;
  text-rendering: optimizeLegibility;
  font-size: 16px !important;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: translate3d(-50%, 25%, 0);
    visibility: visible;
  }

  1% {
    opacity: 1;
    transform: translate3d(-50%, 75%, 0);
  }

  32% {
    opacity: 1;
    transform: translate3d(-50%, 75%, 0);
    visibility: visible;
  }

  33% {
    opacity: 0;
    transform: translate3d(-50%, 135%, 0);
    visibility: hidden;
  }

  to {
    opacity: 0;
    visibility: visible;
  }
}

.double-div-wrapper {
  display: flex;
  justify-content: center;
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
  padding: 50px 0;
}

.work-text-wrapper {
  display: flex;
  flex-direction: column;
  text-align: start;
  padding-left: 40px;
  justify-content: center;
  font-size: 16px;
}

.header {
  font-family: Inter-black;
  letter-spacing: -1px;
  font-size: 30px;
  text-transform: uppercase;
  letter-spacing: -2px;
}

.subheader {
  font-family: Inter-regular;
  letter-spacing: -1px;
  color: gray;
  margin: 10px 0px;
}

.tech-l {
  display: inline-block;
  margin: 3px 3px 3px 0;
  font-family: Inter-bold;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 2px 7px;
  border-radius: 3px;
  color: #ff65bf;
  border: 1px solid #ff65bf;
  background: 0 0;
  cursor: default;
}

.tech-jc {
  display: inline-block;
  margin: 3px 3px 3px 0;
  font-family: Inter-bold;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 2px 7px;
  border-radius: 3px;
  color: #ffbb00;
  border: 1px solid #ffe600;
  background: 0 0;
  cursor: default;
}

.tech-rf {
  display: inline-block;
  margin: 3px 3px 3px 0;
  font-family: Inter-bold;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 2px 7px;
  border-radius: 3px;
  color: #0d45ff;
  border: 1px solid #0d45ff;
  background: 0 0;
  cursor: default;
}

.tech-project {
  display: inline-block;
  margin: 3px 3px 3px 0;
  font-family: Inter-bold;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 2px 7px;
  border-radius: 3px;
  color: #505050;
  border: 1px solid #505050;
  background: 0 0;
  cursor: default;
}

.work-section {
  margin: 50px 0;
}

.other-wrapper {
  display: flex;
  justify-content: center;
  padding: 100px 250px;
}

.left-divver {
  flex-shrink: 0;
  width: 200px;
  margin-right: 70px;
  color: #2c2c2c;
  text-transform: uppercase;
  text-align: right;
  font-weight: 700;
  letter-spacing: -1px;
}

.right-divver {
  font-weight: 300;
  font-size: 1rem;
  width: 100%;
  max-width: 650px;
  text-align: start;
}

.project {
  margin-bottom: 50px;
}

.p_name > a {
  text-decoration: none;
  color: black;
  font-family: Inter-black;
  letter-spacing: -1px;
  font-size: 16px;
}

p {
  font-family: Inter-regular;
  letter-spacing: -1px;
  color: gray;
}

.background-para {
  font-family: Inter-regular;
  letter-spacing: -1px;
  color: gray;
  margin: 0;
}

svg {
  height: 16px;
  margin-left: 10px;
}

.tag {
  text-decoration: none;
  color: black;
  font-weight: 900;
  display: inline-block;
  position: relative;
}

.tag::after {
  box-sizing: border-box;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 1px;
  background: #36363c;
  content: "";
  opacity: 0;
  transition: height 0.3s, opacity 0.3s, transform 0.3s;
  transition: height 0.3s, opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
  transform: translateY(-5px);
}

.tag:hover::after {
  height: 2px;
  border-radius: 3px;
  opacity: 1;
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
}

.ul-languages,
.ul-frameworks,
.ul-tools,
.ul-design,
.ul-infrastructure,
.ul-cl {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.ul-languages > li,
.ul-frameworks > li,
.ul-tools > li,
.ul-design > li,
.ul-infrastructure > li,
.ul-cl > li {
  margin-bottom: 5px;
}
.label {
  padding-bottom: 10px;
  font-family: Inter-bold;
  letter-spacing: -1px;
}
.category {
  margin-right: 20px;
}

.skills {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 0fr));
}

.job {
  margin-bottom: 25px;
}

.time-place {
  display: flex;
  justify-content: space-between;
}

.company {
  font-family: Inter-bold;
  letter-spacing: -1px;
}

.position {
  font-family: Inter-regular;
  font-weight: 100;
}

.time {
  font-family: Inter-regular;
}

@media (min-width: 600px) and (max-width: 800px) {
  img {
    filter: drop-shadow(20px 20px 18px rgba(211, 211, 211, 0.507));
  }
  .body-container-padder {
    flex-wrap: wrap;
    padding-top: 0px;
  }
  .header-wrapper {
    position: sticky;
    position: -webkit-sticky;
    top: 0px;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    z-index: 9;
  }

  .double-div-wrapper {
    flex-direction: column;
  }

  .other-wrapper {
    display: inline-block;
    padding: 10px 50px;
  }

  .left-divver {
    text-align: start;
    margin-bottom: 50px;
    margin: 40px auto 50px 0px;
  }
  .work-text-wrapper {
    width: 80%;
    margin-top: 30px;
  }
}

@media (min-width: 500px) and (max-width: 600px) {
  img {
    filter: drop-shadow(20px 20px 18px rgba(211, 211, 211, 0.507));
  }

  .body-container-padder {
    flex-wrap: wrap;
  }

  .header-wrapper {
    position: sticky;
    position: -webkit-sticky;
    top: 0px;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    z-index: 9;
  }

  .ul-wrapper {
    width: 100%;
  }
  .logo {
    line-height: 53px;
    color: rgb(52, 62, 73);
    text-decoration: none;
  }

  .unordered {
    list-style-type: none;
    align-items: center;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
    padding-inline-start: 20px;
  }

  .unordered > li {
    margin-right: 40px;
    font-family: Inter-regular;
    font-weight: 100;
    text-decoration: none;
    text-align: start;
  }

  .header-blur {
    display: flex;
  }

  .double-div-wrapper {
    flex-direction: column;
  }
  .other-wrapper {
    display: inline-block;
    padding: 10px 50px;
  }

  .left-divver {
    text-align: start;
    margin-bottom: 50px;
    margin: 40px auto 50px 0px;
  }
  .work-text-wrapper {
    width: 80%;
    margin-top: 30px;
  }
}

@media (min-width: 200px) and (max-width: 500px) {
  a > img {
    filter: drop-shadow(0px 0px 10px rgba(211, 211, 211, 1.507));
  }

  .header-wrapper {
    position: sticky;
    position: -webkit-sticky;
    top: 0px;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
    z-index: 9;
  }

  .double-div-wrapper {
    flex-direction: column;
  }

  .luvstay-image,
  .just-chat-image,
  .registration-image {
    width: 90%;
    height: 180px;
  }

  .work-text-wrapper {
    width: 80%;
    margin-top: 30px;
  }

  .body-container-padder {
    flex-wrap: wrap;
    padding-top: 0;
    padding-bottom: 2rem;
  }
  .text-wrapper {
    font-size: 30px;
  }
  .get-in-touch {
    font-size: 12px;
  }
  .main-emoji {
    font-size: 150px;
  }

  .shadow-game {
    padding: 5px 5px;
  }
  .logo {
    color: rgb(52, 62, 73);
    text-decoration: none;
    line-height: 53px;
  }
  .logo-wrapper {
    text-align: start;
    padding-left: 20px;
  }
  .ul-wrapper {
    width: 100%;
  }

  .unordered {
    list-style-type: none;
    align-items: center;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    padding-inline-start: 20px;
  }

  .unordered > li {
    margin-right: 40px;
    font-family: Inter-regular;
    font-weight: 100;
    text-decoration: none;
    text-align: start;
  }

  .header-blur {
    display: block;
  }

  .other-wrapper {
    display: inline-block;
    padding: 10px 50px;
  }

  .left-divver {
    text-align: start;
    margin-bottom: 50px;
    margin: 40px auto 50px 0px;
  }

  .jobs {
    font-size: 14px;
  }
}
